.login {
    margin-top: 100px;
    padding: 56px;
    margin-left: 15px;

  .img-responsive {
    margin: 0 auto 30px;
  }
  .forget {
    color: #999;
  }
  .forget-link {
    cursor: pointer;
  }
}

@media (max-width: 767px) {
.login {
    margin-top:15px;
    padding: 16px;
  .forget-info {
    position: absolute;
    top: 25%;
    left: 10px;
    right: 10px;
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 20px;
    background-color: #000;
    color: #fff;
    opacity: 0.9;
    filter: alpha(opacity=90);
  }
}
}

@media (min-width: 768px) {
.login  {
  .img-responsive{
    width: 60% \9;
    max-width: 60%;
  }
}
}
.caret.active {
  border-left: 4px dashed;
  border-top: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid transparent;
}

.no-border {
  border: none !important;
}

@mixin animation($animation...) {
  -webkit-animation: $animation;
      -moz-animation: $animation;
       -o-animation: $animation;
          animation: $animation;
}

.fly-in {
  @include animation(fly-in .8s ease);
}

@-webkit-keyframes fly-in {
  0% {transform: translateY(-100%);}
  100% {transform: translateY(0%);}
}

@-moz-keyframes fly-in {
  0% {transform: translateY(-100%);}
  100% {transform: translateY(0%);}
}

@keyframes fly-in {
  0% {transform: translateY(-100%);}
  100% {transform: translateY(0%);}
}

.shake {
   @include animation(shake .2s ease 2);
}
@-webkit-keyframes shake {
  0% {transform: translateX(0);}
  25%{transform: translateX(-10%);}
  50%{transform: translateX(0);}
  75%{transform: translateX(10%);}
  100% {transform: translateX(0);}
}

@-moz-keyframes shake {
 0% {transform: translateX(0);}
  25%{transform: translateX(-10%);}
  50%{transform: translateX(0);}
  75%{transform: translateX(10%);}
  100% {transform: translateX(0);}
}

@keyframes shake {
  0% {transform: translateX(0);}
  25%{transform: translateX(-5%);}
  50%{transform: translateX(0);}
  75%{transform: translateX(5%);}
  100% {transform: translateX(0);}
}

.ico-groups {
  position: relative;
  text-align: left;
  font-style: normal;
  font-weight: normal;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.ico-groups .ico {
  display: inline-block;
  position: absolute;
  margin: 10px;
  width: 20px;
  height: 20px;
  vertical-align: text-bottom;
}
.ico-groups .ico-label {
  display: inline-block;
  position: absolute;
  margin: 13px;
  width: 70px;
  height: 20px;
  vertical-align: text-bottom;
}
.ico-groups .ico-text {
  height: 40px;
  padding-left: 42px;
  padding-top: 9px;
  padding-bottom: 9px;
}
.ico-groups .first-ico-text {
  height: 40px;
  padding-left: 90px;
  padding-top: 9px;
  padding-bottom: 9px;
}
.ico-user {
  background: url(/w20e/common/img/man.png) center center no-repeat;
}
.ico-password {
  background: url(/w20e/common/img/key.png)center center no-repeat;
}

